<template>
  <div class="main_wrap">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card
      :body-style="{
        padding: 0,
        height: '100%',
      }"
    >
      <div class="main">
        <div class="search">
          <!--  -->
          <el-row>
            <el-col :span="8">
              <el-input
                placeholder="ID"
                v-model="search_id"
                @clear="getOrderList"
                clearable
              >
                <el-button
                  slot="append"
                  icon="el-icon-search"
                  @click="queryOrder"
                ></el-button>
              </el-input>
            </el-col>
          </el-row>
        </div>
        <div class="content" v-loading="loading">
          <!--  -->
          <el-table
            :data="orderList"
            border
            stripe
            @row-click="getOrderDetails"
          >
            <!--  -->
            <el-table-column type="index" label="#"></el-table-column>
            <!--  -->
            <el-table-column
              label=""
              prop="order_number"
              show-overflow-tooltip
            ></el-table-column>
            <!-- id -->
            <el-table-column
              label=" ID"
              prop="order_id"
              width="70px"
            ></el-table-column>
            <!--  -->
            <el-table-column
              label=""
              prop="order_price"
              width="80px"
            ></el-table-column>
            <!--  -->
            <el-table-column label="" prop="pay_status" width="100px">
              <template slot-scope="scope">
                <!--  -->
                <el-tag v-if="scope.row.pay_status === '1'" type="success"
                  ></el-tag
                >
                <el-tag v-else type="danger"></el-tag>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column label="" prop="is_send" width="90px">
            </el-table-column>
            <!--  -->
            <el-table-column
              label=""
              prop="create_time"
              show-overflow-tooltip
            >
              <template slot-scope="scope">
                <!--   -->
                {{ scope.row.create_time | dateFormat }}
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column label="" width="130px">
              <template slot-scope="scope">
                <!--  -->
                <el-tooltip
                  class="item"
                  effect="dark"
                  content=""
                  placement="top"
                >
                  <el-button
                    type="primary"
                    icon="el-icon-edit"
                    size="mini"
                    @click="showBox(scope.row)"
                  ></el-button>
                </el-tooltip>
                <!--  -->
                <el-tooltip
                  class="item"
                  effect="dark"
                  content=""
                  placement="top"
                >
                  <el-button
                    type="success"
                    icon="el-icon-location"
                    size="mini"
                    @click="showProgressBox"
                  ></el-button>
                </el-tooltip>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="pagination">
          <!--  -->
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.pagenum"
            :page-sizes="[5, 10, 15]"
            :page-size="queryInfo.pagesize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
            background
          >
          </el-pagination>
        </div>
      </div>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addressVisible"
      width="50%"
      @close="addressDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addressForm"
        :rules="addressFormRules"
        ref="addressFormRef"
        label-width="100px"
        class="demo-ruleForm"
      >
        <!-- / -->
        <el-form-item label="/" prop="address1">
          <el-cascader
            :options="cityData"
            v-model="addressForm.address1"
          ></el-cascader>
        </el-form-item>
        <!--  -->
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2" clearable></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addressVisible = false"> </el-button>
        <el-button type="primary" @click="editAddress()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%">
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.time"
          color="#0bbd87"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="orderDetailsVisible" width="50%">
      <el-table :data="orderGoods" border style="width: 100%">
        <el-table-column prop="goods_id" label="ID"> </el-table-column>
        <el-table-column prop="goods_price" label=""> </el-table-column>
        <el-table-column prop="goods_number" label=""> </el-table-column>
        <el-table-column prop="goods_total_price" label="">
        </el-table-column>
      </el-table>
    </el-dialog>
  </div>
</template>
<script>
/* eslint-disable */
// 
import cityData from './citydata.js'
export default {
  name: 'order',
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5,
      },
      // 
      total: 0,
      // 
      orderList: [],
      // 
      addressVisible: false,
      // 
      addressForm: {
        address1: [],
        address2: '',
      },
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' },
        ],
        address2: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
      },
      // citydata 
      cityData,
      // 
      progressVisible: false,
      // 
      progressInfo: [],
      search_id: '',
      // 
      loading: true,
      orderDetailsVisible: false,
      orderGoods: {},
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      try {
        const { data: res } = await this.$http.get('orders', {
          params: this.queryInfo,
        })

        if (res.meta.status !== 200) {
          this.loading = false
          return this.$message.error('')
        }
        // 
        this.total = res.data.total
        this.orderList = res.data.goods
        // 
        this.loading = false
      } catch (err) {
        this.$message.error('')
      }
    },
    // 
    handleSizeChange(newSize) {
      this.loading = true
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(newPage) {
      this.loading = true
      this.queryInfo.pagenum = newPage
      this.getOrderList()
    },
    // 
    showBox() {
      this.addressVisible = true
    },
    // 
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    // 
    editAddress() {
      this.addressVisible = false
      return this.$message.error('')
    },
    // 
    async showProgressBox() {
      try {
        // 
        const { data: res } = await this.$http.get('/kuaidi/1106975712662')

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.progressInfo = res.data
        // 
        this.progressVisible = true
      } catch (err) {
        this.$message.error('')
      }
    },
    // 
    async queryOrder() {
      try {
        if (this.search_id.trim() == '') return this.$message.error('ID')
        const { data: res } = await this.$http.get('/orders/' + this.search_id)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        }
        //
        this.orderList = [res.data]
        this.total = 1
      } catch {
        this.$message.error('')
      }
    },
    // 
    async getOrderDetails(order, cloumn) {
      if (cloumn.label == '') return
      const id = order.order_id
      try {
        const { data: res } = await this.$http.get('/orders/' + id)

        this.orderGoods = res.data.goods
        this.orderDetailsVisible = true
      } catch (err) {
        this.$message.error('')
      }
    },
  },
}
</script>
<style lang="less" scoped>
.main_wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  .el-card {
    height: 100%;

    .main {
      display: flex;
      padding: 20px;
      height: 100%;
      flex-direction: column;

      > .search {
        flex: 0 0 40px;
      }

      > .content {
        flex: 1;
        overflow: auto;
      }

      > .pagination {
        flex: 0 0 87px;
        margin-top: auto;
      }
    }
  }
}
.el-cascader {
  width: 100%;
}
</style>
