<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!-- EChartsDom -->
      <div id="main" v-loading="loading"></div>
    </el-card>
  </div>
</template>
<script>
// echarts
import * as echarts from 'echarts'
import _ from 'loadsh'
export default {
  name: 'report',
  data() {
    return {
      // echarts
      options: {
        title: {
          text: '',
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#999',
            },
          },
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: [
          {
            boundaryGap: false,
          },
        ],
        yAxis: [
          {
            type: 'value',
          },
        ],
      },
      loading: false,
      // 
      chart: null,
    }
  },
  mounted() {
    this.initEchart()
    // 
    window.addEventListener('resize', this.handleResize)
  },
  // 
  beforeDestroy() {
    // 
    window.removeEventListener('resize', this.handleResize)
    // 
    this.chart && this.chart.dispose()
  },
  methods: {
    async getData() {
      // 
      try {
        const { data } = await this.$http.get('reports/type/1')
        if (data.meta.status !== 200) {
          this.$message.info(data.meta.msg)
          return null
        } else {
          return data.data
        }
      } catch (error) {
        this.$message.error('')
      }
      //  ()
    },
    async initEchart() {
      this.loading = true
      const echartsData = await this.getData()
      if (echartsData) {
        // 
        this.chart = echarts.init(document.getElementById('main'))
        const result = _.merge(echartsData, this.options)
        // hover
        result.series.forEach((item) => {
          item.emphasis = {
            focus: 'series',
          }
        })
        // 
        this.chart.setOption(result)
      }
      this.loading = false
    },
    // 
    handleResize() {
      this.chart && this.chart.resize()
    },
  },
}
</script>

<style lang="less" scoped>
#main {
  width: 100%;
  height: 420px;
}
</style>
